import React from "react";
import Card from "./Card";

const listings = ({ lisitngs }) => {
  const getLisitngs = () => {
    let listingsOnPage = [];
    let result = [];

    lisitngs.map((lisitng) => {
      return listingsOnPage.push(
        <Card
          title={lisitng.title}
          address={lisitng.address}
          city={lisitng.city}
          state={lisitng.state}
          price={lisitng.price}
          sale_type={lisitng.sale_type}
          home_type={lisitng.home_type}
          bedrooms={lisitng.bedrooms}
          bathrooms={lisitng.bathrooms}
          sqft={lisitng.sqft}
          photo_main={lisitng.photo_main}
          slug={lisitng.slug}
        ></Card>
      );
    });

    for (let i = 0; i < lisitngs.length; i += 3) {
      result.push(
        <div className="row">
          <div className="col-1-of-3">{listingsOnPage[i]}</div>
          <div className="col-1-of-3">
            {listingsOnPage[i + 1] ? listingsOnPage[i + 1] : null}
          </div>
          <div className="col-1-of-3">
            {listingsOnPage[i + 2] ? listingsOnPage[i + 2] : null}
          </div>
        </div>
      );
    }

    return result;
  };

  return <div>{getLisitngs()}</div>;
};

export default listings;
