import React, { useState } from "react";
import axios from "axios";
import Loader from "react-loader-spinner";
import PropTypes from "prop-types";

const ListingForm = (props) => {
  const [formData, setFormData] = useState({
    sale_type: "For Sale",
    price: "₹0+",
    bedrooms: "0+",
    home_type: "House",
    bathrooms: "0+",
    sqft: "1000+",
    days_listed: "1 or less",
    has_photos: "1+",
    keywords: "",
  });

  const {
    sale_type,
    price,
    bedrooms,
    home_type,
    bathrooms,
    sqft,
    days_listed,
    has_photos,
    keywords,
  } = formData;
  const [loading, setLoading] = useState(false);
  const onChange = (e) =>
    setFormData({ ...formData, [e.target.name]: e.target.value });

  const onSubmit = (e) => {
    e.preventDefault();
    axios.defaults.headers = {
      "Content-Type": "application/json",
    };
    setLoading(true);
    axios
      .post("http://rishabkoul.pythonanywhere.com/api/listings/search/", {
        sale_type,
        price,
        bedrooms,
        home_type,
        bathrooms,
        sqft,
        days_listed,
        has_photos,
        keywords,
      })
      .then((res) => {
        setLoading(false);
        console.log(res.data);
        props.setListings(res.data);
        window.scrollTo(0, 0);
      })
      .catch((err) => {
        setLoading(false);
        window.scrollTo(0, 0);
      });
  };
  return (
    <form className="lisitngform" onSubmit={(e) => onSubmit(e)}>
      <div className="row">
        <div className="col-1-of-6">
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="sale_type">
              Sale or Rent
            </label>
            <select
              className="lisitngform__select"
              name="sale_type"
              onChange={(e) => onChange(e)}
              value={sale_type}
            >
              <option>For Sale</option>
              <option>For Rent</option>
            </select>
          </div>
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="sqft">
              Sqft
            </label>
            <select
              className="lisitngform__select"
              name="sqft"
              onChange={(e) => onChange(e)}
              value={sqft}
            >
              <option>1000+</option>
              <option>1200+</option>
              <option>1500+</option>
              <option>2000+</option>
              <option>Any</option>
            </select>
          </div>
        </div>

        <div className="col-1-of-6">
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="price">
              Minimum Price
            </label>
            <select
              className="lisitngform__select"
              name="price"
              onChange={(e) => onChange(e)}
              value={price}
            >
              <option>₹0+</option>
              <option>₹1,00,00,000+</option>
              <option>₹2,00,00,000+</option>
              <option>₹3,00,00,000+</option>
              <option>₹4,00,00,000+</option>
              <option>₹5,00,00,000+</option>
              <option>₹6,00,00,000+</option>
              <option>₹7,00,00,000+</option>
              <option>Any</option>
            </select>
          </div>
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="days_listed">
              Days Listed
            </label>
            <select
              className="lisitngform__select"
              name="days_listed"
              onChange={(e) => onChange(e)}
              value={days_listed}
            >
              <option>1 or less</option>
              <option>2 or less</option>
              <option>5 or less</option>
              <option>10 or less</option>
              <option>20 or less</option>
              <option>Any</option>
            </select>
          </div>
        </div>

        <div className="col-1-of-6">
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="bedrooms">
              Bedrooms
            </label>
            <select
              className="lisitngform__select"
              name="bedrooms"
              onChange={(e) => onChange(e)}
              value={bedrooms}
            >
              <option>0+</option>
              <option>1+</option>
              <option>2+</option>
              <option>3+</option>
              <option>4+</option>
              <option>5+</option>
            </select>
          </div>
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="has_photos">
              Has Photos
            </label>
            <select
              className="lisitngform__select"
              name="has_photos"
              onChange={(e) => onChange(e)}
              value={has_photos}
            >
              <option>1+</option>
              <option>3+</option>
              <option>5+</option>
              <option>10+</option>
              <option>15+</option>
            </select>
          </div>
        </div>

        <div className="col-1-of-6">
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="home_type">
              Home Type
            </label>
            <select
              className="lisitngform__select"
              name="home_type"
              onChange={(e) => onChange(e)}
              value={home_type}
            >
              <option>House</option>
              <option>Condo</option>
              <option>Townhouse</option>
            </select>
          </div>
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="keywords">
              Keywords
            </label>
            <input
              className="lisitngform__input"
              name="keywords"
              type="text"
              onChange={(e) => onChange(e)}
              value={keywords}
            />
          </div>
        </div>

        <div className="col-1-of-6">
          <div className="lisitngform__section">
            <label className="lisitngform__label" htmlFor="bathrooms">
              Baths
            </label>
            <select
              className="lisitngform__select"
              name="bathrooms"
              onChange={(e) => onChange(e)}
              value={bathrooms}
            >
              <option>0+</option>
              <option>1+</option>
              <option>2+</option>
              <option>3+</option>
              <option>4+</option>
            </select>
          </div>
        </div>

        <div className="col-1-of-6">
          {loading ? (
            <div className="lisitngform__loader">
              <Loader
                type="Oval"
                color="#424242"
                height={50}
                width={50}
              ></Loader>
            </div>
          ) : (
            <button className="lisitngform__button lisitngform__button--primary">
              Save
            </button>
          )}
        </div>
      </div>
    </form>
  );
};

ListingForm.protoTypes = {
  setListings: PropTypes.func.isRequired,
};

export default ListingForm;
